.order{
  padding: 45px 120px 45px 30px;
  box-shadow: 0 0 35px rgba(0, 41, 102, 0.1);
}
.order__inner{
  display: table;
  width: 100%;
}
.order__delete,
.order__details,
.order__qty,
.order__total{
  display: table-cell;
  vertical-align: middle;
}

.order__delete{
  padding: 0 30px 0 0;
  text-align: center;
}
.order__details{
  width: 250px;
}
.order__title,
.order__total-header{
  margin: 0 0 25px;
  font-size: 14px;
  font-weight: 500;
  line-height: 18px;
  text-transform: uppercase;
  color: #ffffff;
}
.order__price{
  display: inline-block;
  padding: 11px;
  border-radius: 5px;
  background-color: #1f1647;
  font-size: 14px;
  font-weight: 500;
  line-height: 18px;
  text-transform: uppercase;
  text-align: center;
  color: #fff;
}
.order__qty{
	width: 110px;
	span{
		display: inline-block;
    width: 26px;
    height: 26px;
    border-radius: 50%;
    background: transparent;
    border: 1px solid #e4e4e4;
    font-size: 14px;
    font-weight: 400;
    line-height: 24px;
		text-align: center;
    color: #ffffff;
    transition: all .3s;
    cursor: pointer;

		&:hover{
      background: #fff;
			color: #000000;
		}
	}
	input{
    display: inline-block;
    margin: 0;
		width: 50px;
		height: 26px;
		border-width: 0;
		font-size: 14px;
		font-weight: 500;
    line-height: 26px;
		color: #fff;
    text-transform: none;
		text-align: center;
	}
}
.order__total{
  vertical-align: top;
  text-align: right;
}
.order__total-header{
  text-align: right;
}
.order__total-price{
  font-size: 18px;
  font-weight: 500;
  line-height: 18px;
  text-transform: uppercase;
  color: #fff;
}