.arcticmodal{
	position: relative;
	min-width: 300px;
  max-width: 750px;
  padding: 65px 0;
  margin: 0 auto;
  background: #000028;
}
.arcticmodal-close{
	position: absolute;
	right: 24px; top: 24px;
	cursor: pointer;
}
.arcticmodal--wide{
	max-width: 1140px;
}
.arcticmodal-content{
  display: table;
  width: 100%;
}
.arcticmodal-content-left,
.arcticmodal-content-right{
  display: table-cell;
  vertical-align: middle;
  width: 50%;
  padding: 20px 60px;
}
.arcticmodal-header{
  padding: 0 60px 20px;
  margin: 0;
  font-size: 24px;
  font-weight: 900;
  line-height: 39.74px;
  text-transform: uppercase;
  color: #fff;
}
.arcticmodal-body{
	padding: 0 50px;
	background: #fff;
}
.arcticmodal-footer{
	padding: 55px 50px 0;
}
.arcticmodal-overlay,
.arcticmodal-container { 
	position: fixed;
	left: 0;
	top: 0;
	right: 0;
	bottom: 0;
	z-index: 1000;
}
.arcticmodal-container { 
	overflow: auto;
	margin: 0;
	padding: 0;
	border: 0;
	border-collapse: collapse;
}
*:first-child+html .arcticmodal-container { 
	height: 100%;
}
.arcticmodal-container_i { 
	height: 100%;
  margin: 0 auto;
  background: transparent;
}
.arcticmodal-container_i2 { 
	padding: 24px;
	margin: 0;
	border: 0;
	vertical-align: middle;
}
.arcticmodal-error { 
	padding: 20px;
	border-radius: 10px;
	background: #000;
	color: #fff;
}
.arcticmodal-loading { 
	width: 80px;
	height: 80px;
	border-radius: 10px;
	background: #000 url(loading.gif) no-repeat 50% 50%;
}