.payment{
	min-height: 425px;
	margin-bottom: 30px;
	padding: 50px 67px;
	border-radius: 25px;
	background-color: #fff;
	background-image: linear-gradient(-124deg, #ffffff 0%, #e5f1fc 100%);
	
	@include media('<768px'){
		min-height: 0;
		padding: 30px 37px;
	}
}
.payment__logo{
	position: relative;
	height: 94px;
	padding-bottom: 20px;
	margin-bottom: 25px;
	text-align: center;
	&:after{
		content: '';
		position: absolute;
		bottom: 0; left: 0; right: 0;
		height: 1px;
		background-color: #e8e8e8;
	}
	img{
		display: block;
		margin: 0 auto;
		max-width: 100%;
		max-height: 100%;
	}
}
.payment__title{
	margin: 0 0 16px;
	font-size: 18px;
	font-weight: 900;
	line-height: 18px;
	color: #000000;
}
.payment__text{
	margin: 0;
	font-size: 14px;
	line-height: 18px;
	font-weight: 400;
	color: #000000;
}