.benefits{
  position: relative;
  padding-top: 118px;
}
.benefits__header{
  position: absolute;
  top: 0;
  font-size: 260px;
  font-weight: 900;
  line-height: 260px;
  text-transform: uppercase;
  text-shadow: 0 15px 35px rgba(0, 7, 40, 0.05);
  color: #f4f8fc;
  white-space: nowrap;
  z-index: 1;
}
.benefits__title{
  position: relative;
  z-index: 5;
  margin: 0 0 21px;
  font-size: 24px;
  font-weight: 900;
  line-height: 24px;
  text-transform: uppercase;
  color: #000;
}
.benefits__text{
  position: relative;
  z-index: 5;
  margin: 0 0 50px;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  color: #000;
}