.sort{
  padding: 22px 0 60px;
  font-size: 14px;
  font-weight: 500;
  line-height: 20px;
  text-align: right;
  color: #fff;
}
.sort__text{
  display: inline-block;
  margin-right: 16px;
  vertical-align: middle;
}
.sort__variant{
  display: inline-block;
  vertical-align: middle;
  margin-right: 15px;
  color: #fff;

  &:last-child{
    margin-right: 0;
  }
}