.sektor {
	position: relative;
	display: table;
	width: 265px;
	height: 265px;
	margin: 0 auto;
	box-shadow: 0 12px 28px 7px rgba(0, 246, 255, 0.15);
	border-radius: 50%;
	@include media('<640px'){
		margin: 20px 0 40px;
	}
}
.sektor-value{
	display: table-cell;
	font-size: 58px;
	font-weight: 700;
	line-height: 38.55px;
	vertical-align: middle;
	text-align: center;
	color: #ffffff;
}
.sektor svg{
	display: block;
}
.sektor-svg{
	position: absolute;
	top: 0; left: 0;
	right: 0; bottom: 0;
}
.Sektor-circle{
	stroke-opacity: 0.25;
}