.list{
  display: block;
  margin: 0;
  padding: 0;
  list-style: none;
}
.list__item{
  &.is--inline{
    display: inline-block;
    margin-right: -.25em;
    margin-left: 40px;
    vertical-align: top;
  }
  &:first-child{
    margin-left: 0;
  }
}
.list__link{
  text-decoration: none;
  color: #ffffff;

  &:hover {
    color: #0ff;
  }

  &.is--small{
    font-size: 14px;
    line-height: 20px;
  }
}