
.shares_content_top{
	position: relative;
	top: auto;
	bottom: auto;
	.txt_slider_shares{
		font-size: 18px;
		color: #c7c7cf;
		width: 50%;
		padding-bottom: 90px;
		margin-top: 30px;

		@include media('<=900px'){
			width: 100%;
		}
	}
}

.shares_title_section{
	color: #fff;
}
.slider--shares{
	position: relative;
	top: 0;
	left: 0;
	padding-left: 170px;
	width: 1520px;

	@include media('<=1200px'){
		width: 100%;
		padding-left: 0;
	}
}
.shares_item{
	overflow: hidden;
	position: relative;
	min-height: 520px;
	padding-left: 115px;

	@include media('<=1200px'){
		padding: 0 5px;
	}



	.shares_item_bg{
		position: absolute;
		left: 0;
		right: 0;
		bottom: 15%;
		top: 0;
		background-size: cover;
		border-radius: 30px;
		background-position: 0 0;
		background-repeat: no-repeat;
		z-index: 10;
	}

	.shares_item_title{
		position: relative;
		margin: 140px 0 35px;

		font-size: 36px;
		font-weight: 900;
		line-height: 36px;
		text-transform: uppercase;

		transition: all .3s ease-out;
		color: #fff;
		z-index: 15;

		@include media('<=1200px'){
			text-align: center;
		}
		@include media('<900px'){
			font-size: 28px;
		}

		@include media('<700px'){
			margin-top: 25%;
		}

		@include media('<680px'){
			font-size: 24px;
		}
	}

	.shares_item_action{
		position: relative;

		.shares_btn{
			padding: 25px 60px;
			font-size: 10px;
			max-width: 390px;
			//display: block;
			border-radius: 40px;
			border: 2px solid #02b5ff;
			position: relative;
			z-index: 15;
			line-height: 10px;
			text-align: center;
			color: #fff;
			text-transform: uppercase;
			display: block;
			font-weight: 600;

			@include media('<=1200px'){
				margin-right: auto;
				margin-left: auto;
				padding: 25px 30px;
			}

			&:before{
				content: "";
				position: absolute;
				top: 0;
				left: 0;
				height: 100%;
				width: 100%;
				border-radius: 40px;
				background-image: linear-gradient(90deg,#00b4ff 0,#77e7e7);

				transition: width .4s,opacity .1s cubic-bezier(.4,0,1,1);

				transition-delay: .1s;
				opacity: 0;
				z-index: -1;


			}
			&:hover{
				border-color: rgba(0,0,0,0);
				transition-delay: .1s;
			}
			&:hover::before{
				opacity: 1;

			}
		}
	}

	.time_share{
		position: absolute;
		z-index: 15;
		bottom: 0;
		left: 30px;

		@include media('<=870px'){
			left: 0;
		}
		.time_value{
			font-size: 36px;
			font-weight: bold;
			color: #fff;
			padding: 0 5px 0 20px;

			@include media('<=870px'){
				font-size: 24px;
				padding: 0 5px 0 10px;
				font-weight: normal;
			}
			@include media('<=420px'){
				font-size: 16px;
				padding: 0 3px 0 5px;
			}
		}
		.time_title{
			font-size: 18px;
			color: #a2a1b1;

			@include media('<=870px'){
				font-size: 14px;
			}
			@include media('<=420px'){
				font-size: 12px;
			}
		}

		.main{
			@include media('<=610px'){
				display: block;
				padding-bottom: 10px;
			}


		}
	}
}


.slider__controls--shares{
	position: absolute;
	left: 0; top: 40%;
	width: 120px; height: 48px;
	margin-top: -20px;

	@include media('<=1200px'){
		top: auto;
		padding-left: 0;
		bottom: -25px;
		margin: 0 auto;
		width: max-content;
		height: 40px;
		position: relative;
	}
	.shares__prev,
	.product_popular__prev{
		margin-right: 30px;

		.sprite-left-arrow_shares {
			display: inline-block;
			vertical-align: middle;
			width: 16px;
			height: 12px;
			background: url('../images/sprites.png') 0px -162px/184px 174px no-repeat;
		}
	}

	.shares__next,
	.product_popular__next{
		//margin-right: -100px;
		.sprite-right-arrow_shares {
			display: inline-block;
			vertical-align: middle;
			width: 16px;
			height: 12px;
			background: url('../images/sprites.png') -166px -162px/184px 206px no-repeat;
		}

	}
	.shares__prev,
	.shares__next,
	.product_popular__prev,
	.product_popular__next{
		position: relative;
		margin-bottom: 15px;
		width: 40px; height: 40px;
		line-height: 38px;
		border-radius: 50%;
		border: 1px solid #00ffff;
		text-align: center;
		display: inline-block;
		transition: all .3s ease-out;

		&:hover{
			//box-shadow: inset 0 0 0 2px #000000;
			background-color: #00ffff;
		}
		i{
			vertical-align: middle;
		}
		a {
			position: absolute;
			top: 0; left: 0;
			width: 40px; height: 40px;
			border-radius: 50%;
		}
	}

}

