.goods{
  overflow: hidden;
  position: relative;
  min-height: 360px;
  padding: 50px;
  border-radius: 25px;
  border: 2px solid rgba(0, 168, 255, 0.85);
  text-align: center;
  z-index: 10;

  &:after{
    content: '';
    position: absolute;
    left: 0; right: 0; top: 0; bottom: 0;
    width: 100%; height: 100%;
    background: #ffffff;
    opacity: 1;
    transition: opacity .3s cubic-bezier(0.4, 0, 1, 1);
    z-index: 12;
  }

  &:hover:after{
    opacity: 0;
  }
  
  &:before {
    content: '';
    position: absolute;
    left: 0; right: 0; top: 0; bottom: 0;
    width: 100%; height: 100%;
    box-shadow: 0 10px 25px rgba(0, 0, 0, 0.05);
    background-image: linear-gradient(to top, rgba(0, 0, 40, 0.6) 0%, rgba(30, 21, 76, 0.6) 100%);
    z-index: 8;
  }

  @include media('<992px'){
    &:after{
      opacity: 0;
    }
  }
}
.goods__bg{
  position: absolute;
  left: 0; right: 0;
  bottom: 0; top: 0;
  background-position: 0 0;
  background-repeat: no-repeat;
  z-index: 10;
}
.goods__title{
  position: relative;
  margin: 21px 0 15px;
  min-height: 118px;
  font-size: 24px;
  font-weight: 900;
  line-height: 36px;
  text-transform: uppercase;
  transition: all .3s ease-out;
  color: #000000;
  z-index: 15;
  @include media('<992px'){
    color: #fff;
  }
}
.goods__action{
  position: relative;
  z-index: 15;
  a {
    transition: color .3s ease-out;
    @include media('<992px'){
      color: #fff;
    }
  }
}

.goods:hover{
  .goods__title{
    color: #ffffff;
  }
  .goods__action a{
    color: #ffffff;
  }
}