.share{
  padding: 0 0 0 5px;
  font-size: 18px;
  font-weight: 700;
  line-height: 30px;
  color: #ffffff;
}
.share__text{
  display: inline-block;
  vertical-align: middle;
  margin-right: 40px;
}
.share__item{
  display: inline-block;
  vertical-align: middle;
  margin-right: 30px;
  font-size: 23px;
  font-weight: 700;
  line-height: 30px;
  color: #ffffff;
  &:last-child{
    margin-right: 0;
    margin-left: 30px;
  }
  &:hover{
    color: #0ff;
  }
}