table {
  width: 100%;
  margin-bottom: 30px;
  background: #000028;
  color: #ffffff;

  tr:nth-child(odd){
    background: rgba(#000000, .2)
  }
  th, td{
    padding: 15px 70px;
    font-size: 18px;
    font-weight: 400;
    line-height: 20px;
    text-transform: uppercase;
    color: #ffffff;
  }
}
