.checkout{}
.checkout__form{
  padding-right: 10%;
}
.checkout__order{
  display: inline-block;
  width: 100%;
  vertical-align: middle;
}
.checkout__order-edit{
  padding-top: 15px;
  text-align: right;

  a {
    font-size: 14px;
    font-weight: 500;
    line-height: 20px;
    text-decoration: underline;
    text-transform: none;
    color: #84ffff;
    &:hover{
      text-decoration: none;
      color: #fff;
    }
  }
}
.checkout__order-item{
  width: 100%;
  display: inline-block;
  box-shadow: 0 0 35px rgba(0, 41, 102, 0.1);
  border-radius: 15px;
  background-color: #fff;
  background-image: linear-gradient(to right, #000028 0%, #000028 100%);
}

.checkout__order-product,
.checkout__order-total{
  display: inline-block;
  width: 60%;
  margin-right: -.25em;
  padding: 50px 25px 50px 40px;
}
.checkout__order-total{
  width: 40%;
  border-left: 1px solid rgba(#0ff, .05);
}
.checkout__product{}
.checkout__order-total-price{
  font-size: 18px;
  font-weight: 900;
  line-height: 18px;
  text-transform: uppercase;
  color: #fff;
}
.checkout__order-total-title,
.checkout__product-title{
  margin: 0 0 25px;
  font-size: 14px;
  font-weight: 500;
  line-height: 18px;
  text-transform: uppercase;
  color: #fff;
}
.checkout__order-total-title{
  margin-bottom: 15px;  
}
.checkout__product-price{
  display: inline-block;
  padding: 11px;
  margin-right: 20px;
  border-radius: 5px;
  background-color: #1f1647;
  font-size: 14px;
  font-weight: 500;
  line-height: 18px;
  text-transform: uppercase;
  text-align: center;
  color: #fff;
}
.checkout__product-qty{
  display: inline-block;
  font-size: 14px;
  font-weight: 500;
  line-height: 18px;
  color: #fff;
  text-transform: uppercase;
}