*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
}
body {
  background-color: #000028;
  font-size: 16px;
  line-height: 20px;
  font-family: "Gotham Pro", Verdana, Geneva, Tahoma, sans-serif;
}
a{
  text-decoration: none;
  transition: color .3s ease-out;
}
.container{
  min-width: 320px;
  max-width: 1170px;
	margin: 0 auto;
  padding: 0 15px;
  @include clearfix();
  
  @include media('<=1200px'){
    padding: 0 30px;
  }

}
.read-more{
  position: relative;
  display: inline-block;
  //padding: 0 0 14px;
  padding: 0 0 8px;
  font-size: 14px;
  font-weight: 900;
  line-height: 24px;
  text-transform: uppercase;
  text-decoration: none;
  transition: all .3s;
  color: #000000;
  &:after{
    content: '';
    position: absolute;
    bottom: 0; left: 0;
    width: 40px;
    height: 1px;
    background: #000000;
    transition: all .3s;
  }
}
.read-more--alt{
  color: #0ff;
  @include media('<768px'){
    margin-bottom: 10px;
  }
  &:after{
    background: #0ff;
  }
  &:hover{
    color: #fff;
    &:after{
      width: 100%;
      background: #fff;
    }
  }
}
.read-more:hover{
  color: #0ff;
  &:after{
    width: 100%;
    background: #0ff;
  }
}
.is--relative{
  position: relative;
}
.is--uppercase{
  text-transform: uppercase;
}
.is--center{
  text-align: center;
}
.is--inline{
  display: inline-block;
  vertical-align: middle;
  margin-right: 26px;
  &:last-child{
    margin-right: 0;
  }
}
.is--hidden {
  overflow: hidden;
}
.is--seo-text{
  font-size: 16px;
  line-height: 24px;
}
.is--mt-large{
  margin-top: 27px;
}
.is--white{
  color: #fff!important;
}
.is--text-right{
  text-align: right;
  @include tablet {
    text-align: left;
  }
}
.is--middle{
  vertical-align: middle!important;
}
.is--mb-lg{
  margin-bottom: 50px;
}
.is--float-right{
  float: right;
}

.separator{
  clear: both;
  width: 100%;
  height: 120px;

  &.is--huge{
    height: 200px;
    
    @include media('<992px'){
      height: 120px;
    }
    @include media('<768px'){
      height: 60px;
    }
  }
}