.product-item{
  margin-bottom: 60px;
}
.product-item__inner{
  display: inline-block;
  max-width: 260px;
  width: 100%;
  overflow: hidden;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.2);
  border-radius: 25px;
  background-color: #33348e;
  background-image: linear-gradient(to top, #000028 0%, #1e154c 100%);
  text-align: left;
}
.product-item__discount{
  margin-bottom: 18px;
  font-size: 24px;
  font-weight: 900;
  line-height: 24px;
  text-transform: uppercase;
  text-align: center;
  color: #91cef4;
}
.product-item__info{
  position: relative;
  z-index: 20;
  max-width: 240px;
  padding: 16px 20px;
  margin: -30px auto 15px;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  background-color: #000028;
  background-image: linear-gradient(-268deg, #000028 0%, #1e154c 100%);
  font-size: 14px;
  font-weight: 900;
  line-height: 18px;
  text-transform: uppercase;
  color: #ffffff;

  .sprite-info{
    float: right;
    &:hover{
      @include sprite('info-alt');
    }
  }
}
.product-item__list{
  padding: 0 20px 0 56px;
  margin: 0 0 40px;
  list-style: none;
  font-size: 16px;
  font-weight: 500;
  line-height: 20px;
  color: #ffffff;

  li {
    margin-bottom: 10px;
  }
}
.product-item__price{
  margin: 0 0 49px;
  padding: 0 50px;
  text-align: center;
  del {
    display: inline-block;
    vertical-align: middle;
    margin-right: 30px;
    font-size: 18px;
    font-weight: 400;
    line-height: 24px;
    text-decoration: line-through;
    color: #d1d1d1;
  }
  ins{
    display: inline-block;
    vertical-align: middle;
    text-decoration: none;
    font-size: 30px;
    font-weight: 900;
    line-height: 24px;
    color: #91cef4;
  }
}
.product-item__action{
  padding: 0 30px 30px;
}
.product-item__favoure{
  margin-left: 15px;
  &:hover{
    @include sprite('heart-sm-alt');
  }
}