.steps{
  @extend .row;
  padding-left: 5px;
  position: relative;
  overflow: visible;
  z-index: 10;
}
.steps__progress{
  position: absolute;
  top: 7px; left: 20px;
  z-index: -1;

  @include media('<=1200px'){
    display: none;
  }
}
.steps__item{
  @extend .col;
  margin-bottom: 64px;
  vertical-align: top;
  width: percentage(3/12);

  &.is--last{
    float: right;
  }

  @include media('<=1200px'){
    width: percentage(4/12);
  }
  @include media('<=780px'){
    width: percentage(6/12);
    margin-bottom: 40px;
  }
  @include media('<=600px'){
    width: percentage(12/12);
    margin-bottom: 20px;
  }
}
.steps__item-number{
  width: 67px;
  height: 67px;
  margin-bottom: 25px;
  line-height: 67px;
  box-shadow: 0 21px 35px rgba(0, 0, 32, 0.35);
  background-color: #000028;
  border-radius: 10px;
  font-size: 24px;
  font-weight: 500;
  text-align: center;
  color: #ffffff;
  @include media('<=600px'){
    float: left;
    margin: 0 10px 0 0;
    width: 32px;
    height: 32px;
    font-size: 16px;
    line-height: 32px;
  }
  &.is--active{
    color: #0ff;

    i {
      @include sprite('happy-alt');
    }
  }
}
.steps__item-text{
  font-size: 18px;
  font-weight: 500;
  line-height: 24px;
  color: #ffffff;
  @include media('<=600px'){
    font-size: 16px;
  }
}
.steps__action{
  @extend .col;
  @extend .btn-action;
  width: 360px;
  margin-left: 26px;
  margin-top: 5px;
  margin-bottom: 64px;
  
  @include media('<=1200px'){
    margin-right: 20px;
    margin-top: 15px;
    margin-left: 20px;
  }
  @include media('<=780px'){
    width: 300px;
    margin-bottom: 40px;
  }
  @include media('<=600px'){
    margin-bottom: 20px;
    width: 240px;
  }
}