.game-accounts{
  position: relative;
  padding: 29px 0;

  @include media('<=680px'){
    display: none;
  }
}
.game-accounts__title{
  position: relative;
  padding: 6px 30px 6px 0;
  font-size: 18px;
  font-weight: 500;
  line-height: 20px;
  text-transform: uppercase;
  color: #3ff6f6;
  &:after{
    content: " ";
    position: absolute;
    top: 0;
    bottom: 0;
    right: 7px;
    margin: auto;
    width: 0;
    height: 0;
    border: 4px solid transparent;
    border-top-color: #3ff6f6;
    border-bottom: none;
  }
}
.game-accounts__dropdown{
  display: none;
  position: absolute;
  top: 60px; left: -30px;
  width: 940px;
  min-height: 230px;
  padding: 25px 0;
  border-radius: 20px;
  background-color: #000028;
  box-shadow: 0 0 35px rgba(0, 2, 18, 0.8);
  
  @include media('<=1200px'){
    width: 280px;
  }
}

.game-accounts__dropdown-list{
  position: absolute;
  top: 25px;
  left: 0;
  width: 265px;
  margin: 0;
  padding: 0;
  list-style: none;
  @include media('<=1200px'){
    position: relative;
  }
  &:before{
    position: absolute;
    width: 1px;
    height: 180px;
    background: #ffffff;
    opacity: 0.1;
  }

  &.is--level-2{
    display: none;
    width: 285px;
    top: 0;
    left: 266px;
    &:before{
      content: '';
    }
    @include media('<=1200px'){
      position: relative;
      width: 100%;
      left: auto;
      top: auto;
      &:before{
        display: none;
      }
    }
  }
  &.is--level-3{
    display: none;
    width: 285px;
    top: 0;
    left: 285px;
    &:before{
      content: '';
    }
    @include media('<=1200px'){
      position: relative;
      width: 100%;
      left: auto;
      top: auto;
      &:before{
        display: none;
      }
    }
  }
}
.game-accounts__dropdown-item{
  &.has--submenu:hover > .game-accounts__dropdown-list{
    display: block;
  }
  &:hover > .game-accounts__dropdown-link {
    color: #3ff6f6;
  }
  &.has--submenu > .game-accounts__dropdown-link:after {
    content: " ";
    position: absolute;
    top: 0;
    right: 47px;
    bottom: 0;
    margin: auto;
    width: 0;
    height: 0;
    border: 4px solid transparent;
    border-left-color: #3ff6f6;
    border-right: none;
    transition: all .3s;
  }
  
  &.has--submenu:hover > .game-accounts__dropdown-link:after {
    @include media('<=1200px'){
      transform: rotate(90deg);
    }
  }
}
.game-accounts__dropdown-link{
  position: relative;
  display: block;
  padding: 10px 50px 10px 30px;
  font-size: 18px;
  font-weight: 400;
  line-height: 20px;
  color: #ffffff;

  &:hover{
    color: #3ff6f6;
  }
}