.menu{
	margin-bottom: 5px;
}
.menu__title{
  margin: 0 0 21px;
  font-size: 18px;
  font-weight: 900;
  line-height: 20px;
  text-transform: uppercase;
  color: #ffffff;
}
.menu__list{
	list-style-type: none;
	padding: 0;
	margin: 0;
}
.menu__item{
	position: relative;
	padding-bottom: 10px;
}
.menu__link{
  display: block;
  font-size: 14px;
  font-weight: 500;
  line-height: 20px;
  color: #ffffff;
  text-decoration: none;
  &:hover {
    color: #0ff;
  }
}