.slider{
  position: relative;
  text-align: left;
}
.slider__item{
  position: relative;
  background-color: #000028;
  &:after{
    content: '';
    position: absolute;
    top: 0; right: 0; bottom: 0; left: 0;
    background-image: linear-gradient(to right, rgba(0, 0, 40, 0.5) 0%, rgba(30, 21, 76, 0.5) 100%);
    z-index: 1;
  }
  img{
    display: block;
    max-width: 100%;
  }
}
.slider_item_basket{
	&:after{

		background-image: linear-gradient(to right, rgba(0, 0, 40, 0.8) 0%, rgba(30, 21, 76, 0.8) 100%);

	}
}
.slider__content{
  position: absolute;
  top: 0; left: 0; right: 0;
  width: 100%;
  z-index: 20;

  &.is--relative {
    position: relative;
  }
}
.slider__title{
  margin: 104px 0 0;
  font-size: 58px;
  font-weight: 900;
  line-height: 72px;
  text-shadow: 0 32px 35px rgba(0, 7, 40, 0.92);
  text-transform: uppercase;
  color: #ffffff;
  @include media('<1200px'){
    margin-top: 40px;
    font-size: 42px;
    line-height: 54px;
  }
  @include media('<800px'){
    margin-top: 25px;
    font-size: 30px;
    line-height: 42px;
  }
  @include media('<600px'){
    margin-top: 15px;
    font-size: 18px;
    line-height: 24px;
  }
}
.slider__text{
  max-width: 600px;
  margin-bottom: 125px;
  font-size: 18px;
  font-weight: 400;
  line-height: 24px;
  color: #ffffff;
}
.slider__pagination{
  position: absolute;
  left: 0; right: 0; bottom: 90px;
  text-align: center;
  @include media('<1200px'){
    bottom: 40px;
  }
  @include media('<800px'){
    bottom: 25px;
  }
  @include media('<600px'){
    bottom: 10px;
  }

  a{
    display: inline-block;
    width: 15px;
    height: 15px;
    margin-right: 16px;
    border-radius: 50%;
    border: 1px solid #0ff;
    background: rgba(#0ff, 0);
    transition: all .3s ease-out;

    &:last-child{
      margin-right: 0;
    }

    &:hover,
    &.active{
      background-color: #0ff;
    }
  }
}
.slider--testimonials{
  margin-bottom: 50px;
  @include media('<=1200px'){
    max-width: 760px;
    margin: 0 auto 50px;
  }
  @include media('<=900px'){
    max-width: 360px;
  }
  @include media('<480px'){
    max-width: 300px;
  }
}
.slider--accounts{
  @include media('<=1200px'){
    width: 555px;
    margin: 0 auto;
  }
}
.slider__pagination--testimonials,
.slider__pagination--accounts{
  position: absolute;
  right: 15px; top: 10px;

  a{
    display: inline-block;
    width: 15px;
    height: 15px;
    margin-right: 16px;
    border-radius: 50%;
    background-color: #0ff;
    border: 1px solid #0ff;
    transition: all .3s ease-out;

    &:last-child{
      margin-right: 0;
    }

    &:hover,
    &.active{
      background: rgba(#0ff, 0);
    }
  }

  @include media('<=1600px'){
    right: 30px;
  }
}
.slider--goods{
  position:relative;
  top:0; left:0;
  padding-left: 100px;
  width: 1520px;

  @include media('<640px'){
    padding-left: 0;
    width: 100%;
  }
}
.slider__controls--goods{
  position: absolute;
  left: 0; top: 50%;
  width: 40px; height: 96px;
  margin-top: -48px;

  @include media('<640px'){
    top: auto;
    padding-left: 60px;
    bottom: -60px;
    margin: 0 auto;
    width: 100%; height: 40px;
  }

  .goods__prev,
  .goods__next{
    position: relative;
    margin-bottom: 15px;
    width: 40px; height: 40px;
    line-height: 38px;
    border-radius: 50%;
    border: 1px solid #000000;
    text-align: center;
    transition: all .3s ease-out;

    &:hover{
      box-shadow: inset 0 0 0 2px #000000;
    }
    i{
      vertical-align: middle;
    }
    a {
      position: absolute;
      top: 0; left: 0;
      width: 40px; height: 40px;
      border-radius: 50%;
    }
  }
  @include media('<640px'){
    .goods__prev,
    .goods__next{
      display: inline-block;
    }
    .goods__next{
      margin-right: -100px;
    }
  }
}
