.testimonial{
  margin-bottom: 60px;
}
.testimonial__content{
  padding: 40px 30px;
  margin-bottom: 30px;
  box-shadow: 0 10px 20px 5px rgba(30, 30, 30, 0.05);
  background-color: #ffffff;
  border-radius: 20px;
}
.testimonial__text{
  margin: 0;
  font-size: 14px;
  font-weight: 400;
  line-height: 22px;
  color: #000000;
}
.testimonial__rating{
  margin-bottom: 8px;
}
.testimonial__author{
  font-size: 18px;
  font-weight: 500;
  line-height: 22px;
  color: #ffffff;
}