.social-icons{
  margin-bottom: 30px;
}
.social-icons__link{
	display: inline-block;
	margin-right: 20px;
	border: 1px solid #ffffff;
	border-radius: 20px;
	font-size: 16px;
	line-height: 40px;
	width: 40px;
	height: 40px;
  color: #84ffff;
  text-align: center;
  transition: all .3s;

	&:hover{
		border-color: #84ffff;
	}
}
