.review{
	@include row();
	margin-bottom: 120px;
}
.review__info,
.review__value,
.review__action{
	@include col();
	vertical-align: middle;
}
.review__info{
	width: percentage(5/12);
	@include media('<992px'){
		width: percentage(5/12);
	}
	@include media('<840px'){
		width: percentage(6/12);
	}
	@include media('<640px'){
		width: percentage(12/12);
	}
}
.review__rate{
	position: relative;
	margin-bottom: 30px;

	&:last-child{
		margin-bottom: 0;
	}

	&:after{
		content: '';
		position: absolute;
		right: -50px; top: 50%;
		margin-top: -1px;
		width: 190px;
		height: 1px;
		background-color: #00ffff;

		@include media('<1200px'){
			width: 160px;
		}
		@include media('<1120px'){
			width: 130px;
		}
		@include media('<1030px'){
			width: 100px;
		}
		@include media('<992px'){
			display: none;
		}
	}
}
.review__rate-service{
	display: inline-block;
	width: 180px;
	font-size: 18px;
	font-weight: 700;
	line-height: 24px;
	color: #ffffff;
}
.review__rate-value{
	display: inline-block;
}
.review__value{
	width: percentage(3/12);
	@include media('<992px'){
		width: percentage(5/12);
	}
	@include media('<640px'){
		width: percentage(12/12);
	}
}
.review__action{
	width: percentage(4/12);
	text-align: right;
	@include media('<992px'){
		width: percentage(12/12);
		text-align: left;
	}
}
.review__btn{
	float: right;
	@include media('<992px'){
		float: left;
	}
}
.review__total{
	margin-bottom: 25px;
	font-size: 18px;
	font-weight: 700;
	line-height: 24px;
	text-transform: uppercase;
	color: #ffffff;
}