.article{
  margin-bottom: 30px;
}
.article__img{
  margin: 0 0 38px;

  img{
    display: block;
    max-width: 100%;
    border-radius: 20px;
  }
}
.article__title{
  margin: 0 0 21px;
  font-size: 24px;
  font-weight: 700;
  line-height: 22px;
  color: #ffffff;
}
.article__text{
  margin: 0 0 20px;
  font-size: 14px;
  font-weight: 400;
  line-height: 22px;
  color: #ffffff;
}

.article__link{
  position: relative;
  padding: 0 0 12px;
  font-size: 14px;
  font-weight: 900;
  line-height: 24px;
  text-transform: uppercase;
  text-decoration: none;
  transition: all .3s;
  color: #00f7f7;
  &:after{
    content: '';
    position: absolute;
    bottom: 0; left: 0;
    width: 40px;
    height: 1px;
    background: #00f7f7;
    transition: all .3s;
  }
}
.article__link:hover{
  color: #ffffff;
  &:after{
    width: 100%;
    background: #ffffff;
  }
}