.nav__list{
  margin: 0;
  padding: 0;
  list-style: none;
}
.nav__item{
  display: inline-block;
  vertical-align: middle;
  margin-right: -.25em;
  margin-left: 41px;
  &:first-child{
    margin-left: 0;
  }
  @include media('<=880px'){
    margin-left: 15px;
  }
}
.nav__link{
  display: block;
  font-size: 14px;
  font-weight: 700;
  line-height: 20px;
  text-decoration: none;
  color: #ffffff;
  &:hover{
    color: #0ff;
  }
}