.section--primary_basket{
	background-color: rgba(9, 8, 51, 0.7);
}

.small_img_basket{
	img {
		display: block;
		margin-left: auto;
		margin-right: auto;
		margin-bottom: 25px;
	}
}

.small_txt_basket{
	span{
		color: #f0eff3;
		font-family: 'Gotham Pro';
		font-size: 16px;
		font-weight: 600;
		@include media('<=850px'){
			display: block;
			padding-left: 10px;
		}
		@include media('<=400px'){
			padding-left: 20px;
		}

	}
}

.continue_shop{
	margin-top: 40px;
	margin-bottom: 120px;
}

.padding_left_0{
	padding-left: 0;
}

.padding_right_20{
	padding-right: 20px;
}

.basket_total{
	width: 375px;
	height: 675px;
	box-shadow: 12px 12px 35px 7px rgba(0,0,0,0.15);


	.basket_total_form{

		.title_form{
			height: 80px;
			border-top-left-radius: 30px;
			border-top-right-radius: 30px;
			background-image: linear-gradient(90deg, #060633, #201853);
			box-shadow: 12px 12px 35px 7px rgba(0,0,0,0.15);
			font-weight: 800;
			font-size: 24px;
			font-family: "Gotham Pro", Verdana, Geneva, Tahoma, sans-serif;
			color: #f0eff3;
			text-transform: uppercase;
			.txt_title{
				padding-left: 32px;
				line-height: 80px;
			}
			.price_title{
				float: right;
				line-height: 80px;
				padding-right: 55px;
			}
		}

		.content_form{
			border: 1px solid #33348e;
			height: 595px;
			border-top: none;
			border-bottom-left-radius: 30px;
			border-bottom-right-radius: 30px;
			padding: 40px 32px;
			position: relative;

			.coupon_value{
				width: 100%;
				height: 50px;
				border-radius: 30px;
				border: 1px solid #2c3e68;
				background-color: rgba(3,3,43,0.25);
				margin-bottom: 30px;
			}

			.coupon_title{
				color: #f0eff3;
				font-size: 16px;
				text-transform: uppercase;
				font-weight: 800;
				font-family: "Gotham Pro", Verdana, Geneva, Tahoma, sans-serif;
				font-style: normal;
				padding-left: 20px;
				margin-bottom: 18px;

			}

			input[type=checkbox] {

				display: none;

			}

			input[type=checkbox] + label:before {
				content: '';
				width: 30px;
				height: 30px;
				border-radius: 5px;
				border: 1px solid #00ffff;
				position: absolute;
				left: 50px;
				display: block;
			}

			input[type=checkbox]:checked + label:before {
				content: "\2713";
				text-shadow: 1px 1px 1px rgba(0, 0, 0, .2);
				font-size: 18px;
				color: #f3f3f3;
				text-align: center;
				line-height: 30px;
			}

			.coupon_check_label{
				padding-left: 70px;
				line-height: 30px;
				font-family: "Gotham Pro", Verdana, Geneva, Tahoma, sans-serif;
				font-size: 14px;
				color: #f0eff3;
				font-style: normal;
				cursor: pointer;
			}

			.coupon_btn_sbm{
				display: block;
				width: 100%;
				height: 50px;
				border-radius: 30px;
				text-transform: uppercase;
			}
		}
	}
}
