.section{
  padding: 120px 0;
  background: #ffffff;

  &.is--relative{
    position: relative;
  }

  @include media('<=900px'){
    padding: 80px 0!important;
  }

  &.is--tinted{
    position: relative;
    z-index: 10;

    &:before{
      content: '';
      position: absolute;
      left: 0; right: 0;
      top: 0; bottom: 0;
      background-image: linear-gradient(to right, rgba(0, 0, 40, 0.85) 0%, rgba(30, 21, 76, 0.85) 100%);
      z-index: -1;
    }
  }
}
.section__title{
  margin: 0 0 70px;
  font-size: 36px;
  font-weight: 900;
  line-height: 36px;
  text-transform: uppercase;
  color: #000000;
  @include media('<=900px'){
    margin-bottom: 50px;
    font-size: 28px;
    line-height: 32px;
  }
  @include media('<=680px'){
    margin-bottom: 30px;
    font-size: 24px;
    line-height: 28px;
  }
  &.is--white{
    color: #ffffff;
  }
}
.section--primary{
  background-color: #000028;
  background-image: linear-gradient(to right, #000028 0%, #1e154c 100%);
}
.section--secondary{
  background-color: #f4f8fc;
}
.section--third{
  background-color: #ededed;
}
.section--advantages{
  padding: 113px 0 58px;
}
.section--accounts{
  height: 1070px;
  overflow: hidden;
  padding-top: 114px;
  padding-bottom: 0;
  .section__title{
    margin-bottom: 77px;
  }
}
.section--goods{
  padding: 114px 0;
}
.section--partners{
  padding: 115px 0;
}
.section--articles{
  padding: 115px 0 98px;
}
.section--seotext-1,
.section--seotext-2{
  padding: 98px 0 134px;
}
.section--payments{
  padding: 71px 0 86px;

  span{
    margin: 0 27px 27px 0;
    &:last-child{
      margin-right: 0;
    }
  }
}
.section--tabs{
  padding-top: 0;
}
.section--catalog{
