.header{
  position: relative;
  z-index: 100;
  background-color: #000028;
  background-image: linear-gradient(to top, #000028 0%, #000028 100%); 

  @include media('<=540px'){
    .container {
      padding-left: 10px;
      padding-right: 10px;
    }
  }
}
.header__logo{
  display: inline-block;
  margin-right: -.25em;
  width: percentage(2/12);
  vertical-align: top;

  @include media('<=880px'){
    width: percentage(3/12);
  }
  @include media('<=680px'){
    width: percentage(4/12);
    vertical-align: middle;
  }
  @include media('<=540px'){
    width: percentage(3/12);
  }
}
.header__content{
  display: inline-block;
  margin-right: -.25em;
  width: percentage(10/12);
  vertical-align: top;

  @include media('<=880px'){
    width: percentage(9/12);
  }
  @include media('<=680px'){
    width: percentage(8/12);
    vertical-align: middle;
  }
  @include media('<=540px'){
    width: percentage(9/12);
  }
}
.header__top{
  @include clearfix();
  padding: 15px 0;
  border-bottom: 1px solid rgba(#00ffff, .1);

  @include media('<=680px'){
    display: none;
  }
}
.header__bottom{
  @include clearfix();
}
.header__nav{
  float: left;
  padding-left: 10px;
  @include media('<=540px'){
    padding-left: 0;
  }
}
.header__lang{
  float: right;
  padding-right: 10px;
  @include media('<=540px'){
    padding-right: 0;
  }
}
.header__accounts{
  padding-left: 9px;
  float: left;
}
.header__actions{
  float: right;
  text-align: right;
}