.promo{
  position: relative;
  padding: 105px 0 74px;
  z-index: 10;
  &:before{
    content: '';
    position: absolute;
    left: 0; right: 0;
    top: 0; bottom: 0;
    background-image: linear-gradient(to right, rgba(0, 0, 40, 0.85) 0%, rgba(30, 21, 76, 0.85) 100%);
    z-index: 1;
  }
  @include media('<=900px'){
    padding: 90px 0;
  }
  @include media('<=680px'){
    padding: 70px 0;
  }

  &.is--simple{
    padding-top: 0;
    &:before{
      display: none;
    }
  }
}
.promo__text{
  @extend .slider__text;
}
.promo--main{
  overflow: hidden;
}
.promo--main:after{
  content: '';
  position: absolute;
  left: 0; right: 0;
  top: 0; bottom: 0;
  background-image: url('../images/promo/p-1.png');
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  z-index: -1;
}
.promo__title{
  position: relative;
  margin: 0 0 94px;
  padding-right: 200px;
  text-shadow: 0 32px 35px rgba(0, 7, 40, 0.92);
  color: #ffffff;
  font-size: 58px;
  font-weight: 900;
  line-height: 72px;
  text-transform: uppercase;
  z-index: 10;
  @include media('<=1200px'){
    padding-right: 0;
  }
  @include media('<=900px'){
    margin-bottom: 50px;
    font-size: 40px;
    line-height: 54px;
  }
  @include media('<=680px'){
    margin-bottom: 30px;
    font-size: 32px;
    line-height: 40px;
  }

  .is--simple &{
    margin-bottom: 30px;
  }
}
