.stats{
	overflow: hidden;
	position: relative;
	margin-bottom: 45px;
	border-radius: 15px;
	background: url('../images/stats.png') 0 0 no-repeat;
	z-index: 10;
	box-shadow: 0 0 35px rgba(0, 0, 0, 0.25);
	@include media('<992px'){
		background: rgba(30, 21, 76, 0.85);
		text-align: center;
	}
  &:before{
    content: '';
    position: absolute;
    left: 0; right: 0;
    top: 0; bottom: 0;
		background-image: linear-gradient(to right, rgba(30, 21, 76, 0.85) 0%, rgba(30, 21, 76, 0.85) 100%);
    z-index: -1;
  }
}
.stats__item{
	display: inline-block;
	vertical-align: middle;
	margin-right: -0.25em;
	width: percentage(18/100);
	font-size: 18px;
	font-weight: 300;
	line-height: 24px;
	color: #ffffff;
	text-align: center;
	&:last-child{
		width: percentage(28/100);
	}
	strong{
		font-weight: 600;
	}
	@include media('<1050px'){
		&, &:last-child{
			width: percentage(1/5);
		}
	}
	@include media('<992px'){
		&, &:last-child{
			width: percentage(1/3);
		}
	}
	@include media('<768px'){
		width: percentage(1/2);
		&:last-child{
			width: percentage(1/1);
		}
	}
	@include media('<480px'){
		width: percentage(1/1);
	}
}
.stats__item-inner{
	display: inline-block;
	width: 100%;
	padding: 30px 0 30px 15px;
	text-align: left;
	@include media('<1050px'){
		padding: 20px 0 20px 10px;
	}
}
.stats__icon{
	@extend .social-icons__link;
	display: inline-block;
	margin-right: 18px;
	vertical-align: middle;
	border-color: #0ff;
}
.stats__text{
	display: inline-block;
	width: 68%;
	margin-right: -0.25em;
	vertical-align: middle;
}

.stats__action{
	text-align: center;
	.btn:not(:last-child) {
		margin: 0 30px 30px 0;
	}
}