.login{
  margin-right: 0!important;
  &:hover .login__icon{
    @include sprite("login-alt");
  }
}
.login__icon{
  @include sprite("login");
}
.login__dropdown{
  top: 60px; right: -30px;
  padding: 30px 70px;
}
.login__tabs{
  margin-bottom: 50px;
  a {
    display: inline-block;
    margin-right: 60px;
    font-size: 18px;
    font-weight: 900;
    line-height: 20px;
    text-decoration: underline;
    text-transform: uppercase;
    transition: all .3s;
    color: #fff;
  }
  a:last-child{
    margin-right: 0;
  }
  a:hover,
  a.is--active{
    color: #84ffff;
  }
}