.program_item{
	min-height: 550px;
	max-width: 555px;
	padding: 150px 98px 0;
	margin-bottom: 70px;
	border-radius: 30px;
	transition: box-shadow 0.3s;


	&:hover{
		box-shadow: 20px 20px 30px 0px rgba(0,0,0,0.6);
		transition: box-shadow 0.3s;
		//cursor: pointer;
	}

	@include media ('<=550px'){
		padding: 150px 60px 0;
	}

	@include media ('<=440px'){
		padding: 120px 20px 0;
		min-height: 450px;
	}
	.program_item_title{
		font-size: 36px;
		line-height: 50px;
		text-transform: uppercase;
		color: #ffffff;
		font-weight: bold;

		@include media('<=550px'){
			font-size: 32px;
			line-height: 40px;
		}
		@include media('<=440px'){
			font-size: 26px;
			line-height: 32px;
		}
		p{
			margin: 0;

			@include media ('<=550px'){
				display: inline;
			}
		}
	}

	.program_item_text{
		font-size: 18px;
		line-height: 24px;
		color: #dfdee4;
		margin-top: 40px;

		p{
			margin-bottom: 0px;
			margin-top: 12px;
			@include media ('<=550px'){
				display: inline;
			}
		}
	}

	.read_more_program{
		margin-top: 40px;
		color: #38d6db;
		display: block;
		margin-right: -98px;

		@include media('<=550px'){
			margin-right: -60px;
		}

		@include media('<=440px'){
			margin-right: -20px;
		}
		&:after{
			background: #38d6db;
		}

		&:hover::after{
			width: 99%;
		}
		&:before{
			content: "";
			position: absolute;
			bottom: 0;
			left: 0;
			width: 99%;
			height: 1px;
			background: #122046;
		}

	}
}

.program_item_1{
	min-height: 445px;
	padding: 115px 98px 0;

	@include media ('<=550px'){
		padding: 115px 60px 0;
	}

	@include media ('<=440px'){
		padding: 115px 20px 0;
	}
}

.partner_biz_txt_1{

	position: relative;
	.biz_txt_title_1{
		color: #ffffff;
		text-transform: uppercase;
		font-size: 36px;
		line-height: 36px;
		font-weight: bold;
		padding: 75px 0;

		@include media ('<=550px'){
			font-size: 32px;
			line-height: 32px;
		}

		@include media('<=440px'){
			font-size: 26px;
			line-height: 26px;
		}
	}

	.background_txt{
		position: absolute;
		left: -15px;
		right: 0;
		top: 0;
		font-size: 293px;
		line-height: 293px;
		font-weight: 800;
		z-index: -1;
		overflow: hidden;
		text-align: center;
		text-transform: uppercase;
		color: rgba(30,21,76,0.65);
		//background-image: linear-gradient(90deg,rgba(0,0,40,.95) 0,rgba(30,21,76,.95));

	}
	@include media ('<note'){
		text-align: center;
	}

	@include media ('<=550px'){
		padding-top: 0;
	}


}
