.product{
  @include row();
}
.product__header{
  padding-left: 30px;
}
.product__title{
  margin: 0 0 90px;
  text-shadow: 0 32px 35px rgba(0, 7, 40, 0.92);
  font-size: 48px;
  font-weight: 900;
  line-height: 54px;
  text-transform: uppercase;
  color: #fff;

  @include media('<=768px'){
    font-size: 24px;
    line-height: 32px;
  }
}
.product__slider{
  @include col();
  width: percentage(9/12);
  padding-right: percentage(1/12);

  @include media('<=1200px'){
    padding-right: 0;
  }
  @include media('<=1100px'){
    width: percentage(8/12);
  }
  
  @include media('<=992px'){
    width: percentage(12/12);
    margin-bottom: 50px;
  }
}
.product__slider-inner{
  position: relative;
  padding-top: 40px;
  &:after,
  &:before{
    opacity: 0;
    content:'';
    position: absolute;
    left: 50%; top: 50%;
    transition: all 300ms ease;
  }
  &:after{
    width: 70px;
    height: 70px;
    border-radius: 50%;
    margin: -35px 0 0 -35px;
    box-shadow: 0 2px 40px 11px rgba(77, 112, 180, 0.1);
    border: 8px solid rgba(255, 255, 255, 0.27);
    background-color: rgba(0, 5, 54, 0.93);
  }
  &:before{
    @include sprite('magnifying-glass');
    margin: -8px 0 0 -8px;
    z-index: 40;
  }
  &:hover:after{
    opacity: .65;
  }
  &:hover:before{
    opacity: 1;
  }
  @include media('<=768px'){
    padding: 0;
  }
}
.product__slider-controls{
  @include clearfix();
  width: 110px; height: 40px;
  float: right;
  @include media('<=768px'){
    display: none;
  }
}
.product__slider-prev,
.product__slider-next{
  position: relative;
  margin-bottom: 15px;
  width: 40px; height: 40px;
  line-height: 38px;
  border-radius: 50%;
  border: 1px solid #00ffff;
  text-align: center;
  transition: all .3s ease-out;

  &:hover{
    background: #00ffff;
  }
  i{
    vertical-align: middle;
  }
  a {
    position: absolute;
    top: 0; left: 0;
    width: 40px; height: 40px;
    border-radius: 50%;
  }
}
.product__slider-prev{
  float: left;
  .product__slider-prev-icon{
    @include sprite('prev-white');
  }

  &:hover{
    .product__slider-prev-icon{
      @include sprite('left-arrow');
    }
  }
}
.product__slider-next{
  float: right;
  .product__slider-next-icon{
    @include sprite('next-white');
  }
  
  &:hover{
    .product__slider-next-icon{
      @include sprite('right-arrow');
    }
  }
}
.product__slider-thumbs{
  position: relative;
  margin-top: -46px;
  margin-bottom: 45px;
  z-index: 50;
  @include media('<=768px'){
    display: none;
  }

  a {
    position: relative;
    overflow: hidden;
    display: block;
    &:after{
      content: '';
      position: absolute;
      top: 0; left: 0; right: 0; bottom: 0;
      width: 100%; height: 100%;
      box-shadow: 0 10px 25px rgba(0, 0, 0, 0.2);
      border-radius: 25px;
      background-color: #000028;
      background-image: linear-gradient(to top, rgba(0, 0, 40, 0.6) 0%, rgba(30, 21, 76, 0.6) 100%);
      z-index: -1;
    }
  }
}
.product__details{
  @include col();
  margin-top: -33px;
  width: percentage(3/12);
  text-align: right;
  
  @include media('<=1100px'){
    width: percentage(4/12);
  }
  
  @include media('<=992px'){
    width: percentage(12/12);
    text-align: center;
  }
}
.product__details-inner{
  display: inline-block;
  max-width: 260px;
  width: 100%;
  overflow: hidden;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.2);
  border-radius: 25px;
  background-color: #33348e;
  background-image: linear-gradient(to top, #000028 0%, #1e154c 100%);
  text-align: left;
}
.product__discount{
  margin-bottom: 18px;
  font-size: 24px;
  font-weight: 900;
  line-height: 24px;
  text-transform: uppercase;
  text-align: center;
  color: #91cef4;
}
.product__info{
  position: relative;
  z-index: 20;
  max-width: 240px;
  padding: 16px 20px;
  margin: -30px auto 15px;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  background-color: #000028;
  background-image: linear-gradient(-268deg, #000028 0%, #1e154c 100%);
  font-size: 14px;
  font-weight: 900;
  line-height: 18px;
  text-transform: uppercase;
  color: #ffffff;
  
  .sprite-info{
    float: right;
    &:hover{
      @include sprite('info-alt');
    }
  }
}
.product__list{
  padding: 0 20px 0 56px;
  margin: 0 0 40px;
  list-style: none;
  font-size: 16px;
  font-weight: 500;
  line-height: 20px;
  color: #ffffff;

  li {
    margin-bottom: 10px;
  }
}
.product__price{
  margin: 0 0 49px;
  padding: 0 50px;
  text-align: center;
  del {
    display: inline-block;
    vertical-align: middle;
    margin-right: 30px;
    font-size: 18px;
    font-weight: 400;
    line-height: 24px;
    text-decoration: line-through;
    color: #d1d1d1;
  }
  ins{
    display: inline-block;
    vertical-align: middle;
    text-decoration: none;
    font-size: 30px;
    font-weight: 900;
    line-height: 24px;
    color: #91cef4;
  }
}
.product__action{
  padding: 0 30px 30px;
}
.product__favoure{
  margin-left: 15px;
  &:hover{
    @include sprite('heart-sm-alt');
  }
}