/**
 *
 * buttons
 *
 **/
.btn{
  display: inline-block;
  position: relative;
  max-width: 260px;
  width: 100%;
  padding: 25px 60px;
  font-size: 16px;
  font-weight: 900;
  line-height: 20px;
  text-transform: uppercase;
  color: #f4f8fc;
  text-align: center;
  text-decoration: none;
  z-index: 10;

  &:hover{
    border-radius: 35px;
  }

  &:before{
    content: '';
    position: absolute;
    top: 0; left: 0;
    height: 100%;
    width: 100%;
    opacity: 0;
    border-radius: 35px;
    background-color: #1f1647;
    background-image: linear-gradient(to right, #00b4ff 0%, #84ffff 100%);
    transition: width 400ms, opacity 100ms cubic-bezier(0.4, 0, 1, 1);
    transition-delay: 100ms;
    opacity: 0;
    z-index: -1;
  }
  &:hover:before{
    opacity: 1;
  }
  &:active:before{
    background-image: linear-gradient(to left, #00b4ff 0%, #84ffff 100%);
  }
  &:after{
    content: "";
    display: block;
    background: url("../images/btn.svg") 0 0 no-repeat;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0; right: 0;
    bottom: 0; left: 0;
    border-radius: 35px;
    transition: 550ms box-shadow .3s cubic-bezier(0.4, 0, 1, 1);
  }
  &:hover:after{
    background: transparent;
    box-shadow: 0 15px 35px #00052e;
  }

  &.is--small{
    // display: block;
    padding: 20px;
    max-width: 100%;
    min-width: 260px;
    width: auto;
    font-size: 12px;
    &:after{
      background: url("../images/btn-small.svg") 0 0 no-repeat;
    }
    &:hover:after{
      background: transparent;
      box-shadow: 0 15px 35px #00052e;
    }
  }
  &.is--block{
    display: block;
    min-width: 0;
    max-width: 260px;
  }
}

.btn--text-black{
  color: #000000;
}

.btn-simple{
  position: relative;
  display: inline-block;
  max-width: 165px;
  padding: 12px 20px;
  border: 2px solid #00ffff;
  border-radius: 22px;
  font-size: 12px;
  font-weight: 700;
  line-height: 21px;
  text-transform: uppercase;
  text-align: center;
  color: #ffffff;
  transition: all .3s ease;
  z-index: 5;
  &:hover:after{
    opacity: 1;
  }
  &:after{
    content: '';
    position: absolute;
    opacity: 0;
    top: -2px; right: -2px; bottom: -2px; left: -2px;
    border-radius: 22px;
    background-image: linear-gradient(to right, #00b4ff 0%, #84ffff 100%);
    z-index: -1;
    transition: all .3s ease;
  }

  &.is--large{
    padding: 17px 32px;
    border-radius: 30px;
    &:after{
      border-radius: 30px;
    }
  }
}
.btn-link{
  display: inline-block;
  padding: 22px 0;
  font-size: 12px;
  font-weight: 700;
  line-height: 21px;
  text-decoration: underline;
  color: #84ffff;
  &:hover{
    text-decoration: none;
  }
}

.btn-action{
  float: left;
  position: relative;
  padding: 16px 30px;
  margin-top: 15px;
  margin-bottom: 30px;
  box-shadow: 0 15px 35px #00052e;
  border-radius: 36px;
  background-color: #1f1647;
  background-image: linear-gradient(to right, #00b4ff 0%, #84ffff 100%);
  text-shadow: 0 5px 15px #0ea0a0;
  font-size: 18px;
  font-weight: 900;
  line-height: 41px;
  text-transform: uppercase;
  text-align: center;
  text-decoration: none;
  color: #fff;
  z-index: 10;
  transition: 300ms background 300ms ease-out;

  &:hover{
    text-shadow: none;
    background: transparent;
  }
  &:after{
    content: '';
    position: absolute;
    left: -12px; right: -12px;
    top: -12px; bottom: -12px;
    border-radius: 48px;
    background: rgba(255, 255, 255, 0.05);
    z-index: -1;
  }

  &:before{
    content: '';
    position: absolute;
    top: 0; left: 0;
    width: 100%; height: 100%;
    opacity: 0;
    border-radius: 35px;
    z-index: -1;
    transition: all 250ms ease;
  }

  &:hover:before{
    opacity: 1;
    background-color: #1f1647;
    background-image: linear-gradient(to right, #000028 0%, #1e154c 100%);
  }
  &:active:before{
    opacity: 1;
    background-image: linear-gradient(to left, #000028 0%, #1e154c 100%);
  }
}