.faq{
	margin-bottom: 65px;
}
.faq__item{
	margin-bottom: 8px;
}
.faq__title{
	position: relative;
	margin: 0;
	padding: 16px 42px 16px 28px;
	border-radius: 5px;
	border: 1px solid rgba(#00ffff, .2);
	font-size: 24px;
	font-weight: 300;
	line-height: 30px;
	color: #ffffff;
	cursor: pointer;
	transition: border-color 300ms linear;
	@include media('<768px'){
		font-size: 21px;
	}
	@include media('<540px'){
		font-size: 18px;
	}
	
	&:after,
	&:before{
		content: '';
		position: absolute;
		transition: all 300ms linear;
		background: #fff;
	}

	&:before{
		right: 21px; top: 30px;
		width: 26px; height: 2px;
	}
	
	&:after{
		right: 33px; top: 18px;
		width: 2px; height: 26px;
	}
	
	&.is--active{
		border-color: #0ff;
		&:after,
		&:before{
			transform: rotate(45deg);
			background-color: #0090ff;		
		}
	}
}
.faq__content{
	padding: 25px 115px 25px 0;
	font-size: 16px;
	font-weight: 300;
	line-height: 24px;
	color: #ffffff;

	@include media('<768px'){
		padding-right: 0;
	}

	p{
		margin: 0 0 16px;
	}
}