.langs{
  position: relative;
  &:hover .langs__list {
    display: block;
  }
}
.langs__current{
  position: relative;
  padding-right: 20px;
  padding-top: 6px;
  font-size: 14px;
  font-weight: 500;
  line-height: 14px;
  text-transform: uppercase;
  color: #fff;
  &:after{
    content: " ";
    position: absolute;
    top: 6px;
    bottom: 0;
    right: 7px;
    margin: auto;
    width: 0;
    height: 0;
    border: 4px solid transparent;
    border-top-color: #3ff6f6;
    border-bottom: none;
  }
}
.langs__list{
  display: none;
  position: absolute;
  top: 20px; left: -18px;
  padding: 10px;
  box-shadow: 0 21px 35px rgba(0, 0, 32, 0.35);
  background-color: #000028;
  z-index: 10;
}
.langs__item{
  display: block;
  padding: 5px 10px;
  font-size: 14px;
  font-weight: 500;
  line-height: 20px;
  text-transform: uppercase;
  color: #fff;
  text-align: center;
}

span.langs__item {
  color: rgba(#fff, .5);
}

a.langs__item:hover {
  color: #0ff;
}
