.dropdown{
  position: relative;
  display: inline-block;
  vertical-align: middle;
  min-height: 32px;
  padding: 29px 15px;
  margin-right: 30px;
  text-align: left;
  transition: all .3s ease-out;
  &:hover{
    background-color: #010823;
  }
  
  @include media('<=720px'){
    padding: 29px 10px;
    margin-right: 10px;
  }
  @include media('<=580px'){
    margin-right: 0px;
  }
}
.dropdown__menu{
  display: none;
  position: absolute;
  top: 75px;
  min-width: 460px;
  min-height: 230px;
  border-radius: 20px;
  background-color: #000028;
  box-shadow: 0 0 35px rgba(0, 2, 18, 0.8);
}