.tabs{
  max-width: 750px;
  margin: -33px auto 70px;
  box-shadow: 0 2px 45px 12px rgba(0, 120, 255, 0.1);
  border-radius: 33px;
  background-color: #000000;
  background-image: linear-gradient(to right, #000028 0%, #1e154c 100%);
}
.tabs__content{
  display: none;

  &.is--active{
    display: block;
  }
}
.tabs__list{
  overflow: hidden;
  height: 65px;
  margin: 0;
  padding: 0 100px;
  list-style: none;
  text-align: justify;
  &:after{
    content: '';
    display: inline-block;
    width: 100%;
    height: 0;
  }
  
  @include media('<=768px'){
    padding: 0;
    height: auto;
    text-align: center;
    &:after{
      display: none;
    }
  }
}
.tabs__item{
  display: inline-block;
  
  @include media('<=768px'){
    display: block;
  }
}
.tabs__link{
  display: block;
  padding: 22px 10px;
  font-size: 14px;
  font-weight: 700;
  line-height: 21px;
  color: #ffffff;
  
  &.is--active,
  &:hover{
    color: #00bdfb;
  }
  @include media('<=768px'){
    padding: 10px;
  }
}