.region{
	position: relative;
}
.region__payment-note{
	position: absolute;
	right: 0; top: 0;
	width: 50%;
	font-size: 18px;
	font-weight: 400;
	line-height: 30px;
	color: #ffffff;

	p {
		margin: 0 0 25px;
	}

	@include media('<1060px'){
		position: relative;
		width: 100%;
		margin: -30px 0 60px;
	}
}
.region__list{
	display: inline-block;
	vertical-align: middle;
	width: percentage(2/12);
	margin-right: -0.25em;
	padding-bottom: 30px;

	@include media('<992px'){
		width: 100%;
	}
}
.region__item{
	position: relative;
	display: block;
	padding-bottom: 8px;
	margin-bottom: 17px;
	font-size: 18px;
	font-weight: 300;
	line-height: 30px;
	color: #00ffff;

	&:hover,
	&.is--active{
		color: #ffffff;
	}
	
	&:after{
		content: '';
		position: absolute;
		bottom: 0; left: 0; right: 0;
		width: 0;
		height: 3px;
		background-color: #ebebeb;
		transition: 300ms width linear;
	}

	&:hover:after,
	&.is--active:after{
		width: 100%;
		@include media('<992px'){
			width: 80%;
		}
	}
	@include media('<992px'){
		display: inline-block;
		margin-right: -0.25em;
		width: percentage(1/3);
		vertical-align: top;
	}
	@include media('<550px'){
		width: percentage(1/2);
	}
}
.region__map{
	position: relative;
	display: inline-block;
	vertical-align: middle;
	width: percentage(10/12);
	margin-right: -0.25em;
	padding-top: 40px;

	img {
		display: block;
		max-width: 100%;
	}
	@include media('<992px'){
		width: percentage(12/12);
	}
}
.region__map-item{
	display: none;
	position: absolute;
	top: 40px; left: 0; right: 0; bottom: 0;
	width: 100%;
	&.is--active{
		display: block;
	}
}