.account{
  padding-bottom: 450px;
}
.account__title{
  margin: 0 0 20px;
  font-size: 24px;
  font-weight: 900;
  line-height: 30px;
  text-transform: uppercase;
  color: #ffffff;
}
.account__img{
  margin: 0 0 21px;
  overflow: hidden;
  border-radius: 10px;
  box-shadow: 0 15px 25px rgba(0, 5, 28, 0.5);
  border: 1px solid #00ffff;
  background-color: #000028;
  img {
    display: block;
  }
}
.account__filter{
  @include clearfix();
}
.account__filter-text{
  float: left;
  font-size: 16px;
  font-weight: 400;
  line-height: 48px;
  color: #ffffff;
}
.account__filter-select{
  float: right;
  width: 260px;
}