.registr_fast{

	@include media ('<tablet'){
		margin-top: 0;
	}

	@include media ('<=600px'){
		font-size: 14px;
		padding: 8px;
	}
}
.content_partnerprog2{
	margin-bottom: 60px;
	padding-right: 40px;

	@include media ('<=600px'){
		padding-right: 0;
	}
	.txt_seo{
		color: #cdcdd5;
		margin-top: 40px;
		font-size: 14px;
		line-height: 20px;

		@include media ('<tablet'){
			margin-top: 20px;
		}
	}




}
.partnerprog2_form{
	background-color: rgba(9, 8, 51, 0.7);
	margin-top: 80px;
	padding: 100px 0;

	.form_txt_1{
		font-size: 36px;
		line-height: 46px;
		color: #ffffff;
		font-weight: bold;
		margin-bottom: 40px;

		@include media ('<600px'){
			font-size: 28px;
			line-height: 42px;
		}

		@include media ('<520px') {
			font-size: 24px;
			line-height: 35px;
			margin-bottom: 20px
		}
	}

	.form_txt_2{
		font-size: 18px;
		line-height: 24px;
		color: #bcbcc7;
		margin-bottom: 70px;

		@include media ('<600px'){
			font-size: 16px;
		}
	}

	input{
		height: 50px;
		width: 360px;
		border: 1px solid #084668;
		border-radius: 30px;
		padding: 0 18px;
		font-size: 16px;
		text-transform: none;
		//color: #bcbcc7;
		font-weight: normal;
		background-color: rgba(21, 19, 75, 0.5);

		@include media ('<=1290px'){
			width: 100%;
		}
	}

	a.forg_pass{
		margin-top: -10px;
		display: block;
		color: #02359d;
		text-decoration: underline;
		font-size: 16px;
		padding-left: 20px;
		margin-bottom: 40px;
	}

	button.send{
		width: 360px;
		border: 0 solid;
		height: 50px;
		padding: 4px;
		font-size: 16px;
		margin-top: 0;
		margin-left: 0;

		@include media ('<=1290px'){
			width: 100%;
		}

		@include media ('<tablet'){
			width: 360px;
		}

		@include media ('<=437px'){
			width: 100%;
			margin-right: 0;
		}
	}
}
