.advantage{
  margin: 0 0 60px;
}
.advantage__icon{
  width: 67px;
  height: 67px;
  margin: 0 0 38px;
  line-height: 67px;
  box-shadow: 0 21px 35px rgba(0, 0, 32, 0.1);
  background-color: #000028;
  border-radius: 5px;
  text-align: center;
  i{
    vertical-align: middle;
  }
}
.advantage__title{
  margin: 0 0 21px;
  font-size: 24px;
  font-weight: 900;
  line-height: 24px;
  text-transform: uppercase;
  color: #000000;
}
.advantage__text{
  margin: 0 0 21px;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  color: #000000;
}

@include media('<768px'){
  .advantage{
    margin: 0 0 60px;
  }
  .advantage__icon{
    margin: 0 0 20px;
  }
  .advantage__title{
    margin: 0 0 10px;
    font-size: 18px;
    font-weight: 900;
    line-height: 21px;
  }
  .advantage__text{
    margin: 0 0 10px;
    font-size: 14px;
    line-height: 21px;
  }
}