.pagination{
  margin-top: -15px;
  padding-left: 30px;
	font-size: 0;
	line-height: 0;
	text-align: center;

  span,
	a {
		display: inline-block;
		vertical-align: middle;
    width: 30px;
    height: 30px;
    border: 1px solid transparent;
		margin: 0 8px;
		border-radius: 50%;
		background: transparent;
    font-size: 14px;
    font-weight: 700;
    line-height: 28px;
		text-align: center;
		color: #fff;

		&.is--active{
			text-decoration: none;
      border: 1px solid #00ffff;
		}
	}
  a:hover{
    text-decoration: none;
    border: 1px solid #00ffff;
  }
}