.filter{
  padding: 20px 30px 13px;
  margin-bottom: 10px;
  border-radius: 10px;
  border: 1px solid rgba(#0ff, .2);
  background-color: rgba(#082656, .2);
  color: #fff;
}
.filter__header{
  margin: 0 0 20px;
  font-size: 14px;
  font-weight: 900;
  line-height: 30px;
  text-transform: uppercase;
  color: #3ff6f6;

  & + .filter__title{
    margin-top: -6px;
  }
}

.filter.is--collapsible .filter__header{
  position: relative;
  padding-right: 20px;
  font-size: 16px;
  color: #fff;
  cursor: pointer;
  transition: all 300ms linear;

  &:after{
    content: " ";
    position: absolute;
    top: 0;
    bottom: 0;
    right: 5px;
    margin: auto;
    width: 0;
    height: 0;
    border: 4px solid transparent;
    border-top-color: #fff;
    border-bottom: none;
    transition: all 300ms linear;
  }

  &.is--active{
    color: #3ff6f6;
    &:after{
      border-top-color: #3ff6f6;
      transform: rotate(180deg);
    }
  }
}
.filter__title{
  margin: 0 0 15px;
  font-size: 14px;
  font-weight: 900;
  line-height: 20px;
  color: #fff;

  & + input{
    margin-top: -10px;
    margin-bottom: 21px;
  }
}
.filter__list{
  margin: 0 0 45px; padding: 0;
  list-style: none;

  &.is--scrollable{
    width: 100%;
    height: 119px;
	  overflow: auto;
  }

  &:last-child{
    margin-bottom: 0;
  }
}
.filter__item{
  margin-bottom: 15px;
}