/* =====================================================================================================================
// Skin details */

.irs-line-mid,
.irs-line-left,
.irs-line-right,
.irs-bar,
.irs-bar-edge,
.irs-slider {}

.irs {
    height: 20px;
}
.irs-with-grid {
    height: 60px;
}
.irs-line {
    height: 1px; top: 13px;
}
    .irs-line-left {
        height: 1px;
        background: #0ff;
    }
    .irs-line-mid {
        height: 1px;
        background: #0ff;
    }
    .irs-line-right {
        height: 1px;
        background: #0ff;
    }

.irs-bar {
    height: 1px; top: 13px;
    background: #fff;
}
    .irs-bar-edge {
        top: 13px;
        height: 1px; width: 11px;
        background: #0ff;
    }

.irs-shadow {
    height: 1px; top: 34px;
    background: #0f0f0f;
    opacity: 0.15;
}
.lt-ie9 .irs-shadow {
    filter: alpha(opacity=15);
}

.irs-slider {
    width: 16px; height: 16px;
    border-radius: 50%;
    top: 5px;
    background-color: #fff;
    box-shadow: 0 0 0 #fff;
    transition: box-shadow .3s;
}
.irs-slider.state_hover, .irs-slider:hover {
  box-shadow: 0 0 15px #0ff;
  background-color: #00f0ff;
}

.irs-min, .irs-max {
  font-size: 12px;
  font-weight: 500;
  line-height: 30px;
  color: #fff;
}
.lt-ie9 .irs-min, .lt-ie9 .irs-max {}

.irs-from, .irs-to, .irs-single {
    font-size: 12px;
    font-weight: 500;
    line-height: 30px;
    color: #fff;
}
.lt-ie9 .irs-from, .lt-ie9 .irs-to, .lt-ie9 .irs-single {
    background: #0f0f0f;
}

.irs-grid-pol {
    background: #0f0f0f;
}
.irs-grid-text {
    color: #0f0f0f;
}

.irs-disabled {}
