.selected{
  display: inline-block;
  margin-right: 9px;
  margin-bottom: 10px;
  vertical-align: top;
  padding: 10px 13px 10px 14px;
  border-radius: 5px;
  border: 1px solid rgba(#0ff, .2);
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
  color: #fff;
  &:last-child{
    margin-right: 0;
  }
  i{
    margin-left: 18px;
  }

  &:hover i{
    @include sprite('cancel-alt');
  }
}