.cart{
  &:hover .cart__icon{
    @include sprite("shopping-cart-alt");
  }
}
.cart__icon{
  @include sprite("shopping-cart");
}
.cart__dropdown{
  top: 60px; right: -30px;
  padding: 30px 0;
}
.cart__title{
  padding: 0 70px;
  margin: 0 0 10px;    
  font-size: 24px;
  font-weight: 900;
  line-height: 30px;
  text-transform: uppercase;
  color: #ffffff;
}
.cart__text{
  padding: 0 70px;
  margin-bottom: 45px;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  color: #ffffff;
}
.cart__product{
  padding: 45px 70px;
  box-shadow: 0 0 35px rgba(0, 41, 102, 0.1);
  background-color: #000028;
}
.cart__product-title{
  margin: 0 0 20px;
  font-size: 14px;
  font-weight: 500;
  line-height: 18px;
  text-transform: uppercase;
  color: #ffffff;
}
.cart__product-details{
  display: block;
}
.cart__product-qty,
.cart__product-total,
.cart__product-price{
  display: inline-block;
  vertical-align: middle;
  margin: 0 23px 10px 0;
  font-size: 14px;
  font-weight: 500;
  line-height: 20px;
  text-transform: uppercase;
  color: #ffffff;
}

.cart__product-price{
  padding: 10px;
  border-radius: 10px;
  background-color: #1f1647;
}
.cart__action{
  text-align: center;
}